.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0, 0.9);
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  transition: opacity 0.5s;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1001;
  top: 0;
  right: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  /* padding-top: 60px; */
}

.closeIconContainer {
  height: 20vh;
  width: 100%;
  padding-right: 2.5vw;

  display: flex;
  align-items: center;
  justify-content: flex-end;

  /* background-color: blueviolet; */
}

.closeIconCircle {
  height: 3rem;
  width: 3rem;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 50%;
  /* background-color: #313131; */

  cursor: pointer;
  transition: all .25s ease;
}

.closeIconCircle:hover {
  background-color: #313131;
}

.closeIconCircle:hover .closeIcon {
  /* fill: #ffe001; */
}

.linkContainer {
  padding-left: 32px;
}

.link {
  padding: 8px 0;
  font-size: 24px;
  text-decoration: none;
  color: #818181;
  display: block;
  transition: 0.5s;
}

.link:hover {
  color: #ffe001;
}

@media only screen and (max-width: 600px) {
  .closeIconContainer {
    height: 10vh;
  }
} 